<template>
  <div id="demo" :class="[{'collapsed' : collapsed}]">
    <div class="demo">
      {{exchangeRates}}
      <hr style="margin: 50px 0px;border: 1px solid #e3e3e3;">
      <router-view/>
    </div>
    <sidebar-menu
      :menu="menu"
      :collapsed="collapsed"
      @collapse="onCollapse"
      :theme="selectedTheme"
    />
  </div>
</template>

<script>
import { getExchangeRate } from "@/nbrmApi.js";
export default {
  name: "app",
  created() {
    this.loadData();
  },
  data() {
    return {
      exchangeRates: null,
      menu: [
        {
          header: true,
          title: "Курсна листа"
        },
        {
          href: "/",
          title: "Предземи курсна листа",
          icon: "fa fa-user"
        },
        {
          title: "Табеларни прегледи",
          icon: "fa fa-table",
          href: "/table",
          child: [
            {
              href: "/table/date",
              title: "За датум",
              icon: "fa fa-list-alt"
            },
            {
              href: "/table/period",
              title: "За период",
              icon: "fa fa-list-alt",
              child: [
                {
                  href: "/table/period/1",
                  title: "Контиуниран по датум",
                  icon: "fa fa-list-alt"
                },
                {
                   href: "/table/period/2",
                  title: "По пресечно датуми",
                  icon: "fa fa-list-alt"
                }
              ]
            }
          ]
        },
                {
          title: "Графички приказ",
          icon: "fa fa-chart-area",
          href: "/graph",
          child: [
            {
              href: "/graph/1",
              title: "Single",
              icon: "fa fa-chart-area"
            },
            {
              href: "/graph/2",
              title: "Double",
              icon: "fa fa-chart-area"
            },
            {
              href: "/graph/3",
              title: "Quadruple",
              icon: "fa fa-chart-area"
            },
          ]
        },
        {
          href: "/analiza",
          title: "Анализа",
          icon: "fa fa-envelope"
        }
      ],
      collapsed: false,
      themes: ["", "white-theme"],
      selectedTheme: "default-theme"
    };
  },
  methods: {
    onCollapse(val) {
      console.log(`collapsed ${val}`);
      this.collapsed = val;
    },
    loadData() {
      getExchangeRate("30.12.2018", "31.12.2018").then(xml => {
        let decode = require("unescape");
        xml = decode(xml);
        let parseString = require("xml2js").parseString;
        let temp;
        parseString(xml, function(err, result) {
          console.dir(result);
          temp = result;
        });
        this.exchangeRates = temp;
      });
    }
  }
};
</script>

